html{
	color:#000;
	background:#FFF;
}

body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td,footer,article,aside,header,section,nav,figure,figcaption,hgroup{
	margin:0;
	padding:0;
}

table{
	border-collapse:collapse;
	border-spacing:0;
}

fieldset,img{
	border:0;
}

address,caption,cite,code,dfn,em,strong,th,var{
	font-style:normal;
	font-weight:normal;
}

ol,ul{
	list-style:none;
}

caption,th{
	text-align: left;
}

h1,h2,h3,h4,h5,h6{
	font-size:100%;
	font-weight:normal;
}

q:before,q:after{
	content:'';
}

abbr,acronym{
	border:0;
	font-variant:normal;
}

sup{
	vertical-align:text-top;
}

sub{
	vertical-align:text-bottom;
}

input,textarea,select{
	font-family:inherit;
	font-size:inherit;
	font-weight:inherit;
	*font-size:100%;
	border: 0;
	outline: none;
}

legend{
	color:#000;
}

#yui3-css-stamp.cssreset{ display:none}

button{
	outline: none;
	border: 0;
}

a{
	text-decoration: none;
